.bannerSlider {
  padding-bottom: 0;
  position: relative;
  .height {
    height: 70px !important;
    position: absolute;
    width: 100%;
    z-index: 1;
    bottom: 12%;
  }
  @media (min-width: 1020px) {
    .height {
      height: 70px !important;
      bottom: 0 !important;
    }
  }
  .slick-slide {
    height: 570px !important;
    div {
      &:focus {
        outline: none !important;
      }
    }
    img {
      display: initial !important;
    }
    .banner {
      width: 100%;
      position: relative;
      img {
        width: 100%;
        height: 580px !important;
      }
      .bannerBox {
        position: absolute;
        top: 20px;
        width: 92%;
        left: 4%;
        right: 4%;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        height: 530px;
        &.third {
          justify-content: flex-start;
          align-items: center;
          top: 100px;
          .bottom {
            justify-content: center;
          }
          h1 {
            margin-bottom: 20px;
          }
          p {
            margin-bottom: 0;
            &:nth-child(3) {
              margin: 0;
              margin-bottom: 20px;
            }
          }
        }
        h1 {
          overflow: auto;
          img {
            margin-left: 5px;
            width: 70px;
            height: 2px !important;
            display: initial !important;
            vertical-align: middle;
          }
        }
        .bottom {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          .redBtn {
            padding: 10px 80px;
            display: flex;
            align-items: center;
            z-index: 88;
          }
        }
      }
    }
  }
  @media (min-width: 920px) {
    .slick-slide {
      height: 500px !important;
      .banner {
        position: relative;
        img {
          height: 480px !important;
        }
        .bannerBox {
          width: 500px;
          // width: 45%;
          height: fit-content;
          top: 20%;
          &.third {
            left: 8%;
            justify-content: space-between;
            align-items: initial;
            .bottom{
              justify-content: flex-start;
            }
            h1 {
              margin-bottom: 20px;
            }
            p {
              margin-bottom: 0;
              &:nth-child(3) {
                margin: 0;
                margin-bottom: 20px;
              }
            }
          }
          h1 {
            font-size: 50px;
            line-height: 54px;
            margin-bottom: 30px;
            img {
              width: 90px;
              margin-left: 10px;
              height: 5px !important;
            }
          }
        }
      }
    }
  }
}
