          background-color: white;
        }
        h5 {
          position: absolute;
          width: fit-content;
          background-color: white;
          margin: 0;
          padding: 2px;
          font-weight: normal;
          font-size: 12px;
          line-height: 14px;
          z-index: 60;
          color: #667c8a;
          top: -7.5px;
          left: 9px;
        }
        p {
          width: 100%;
          font-weight: normal;
          font-size: 18px;
          line-height: 21px;
          margin: 0;
          color: #667c8a;
          cursor: pointer;
          padding: 11px 14px;
          border: 0.5px solid #637381;
          box-sizing: border-box;
          border-radius: 3px;
          display: flex;
          align-items: center;
        }
        .circle {
          width: 15px;
          height: 15px;
          margin-right: 5px;
          margin-bottom: 1.5px;
        }
        .spanRight {
          position: absolute;
          right: 7%;
        }
      }
      .date {
        width: 100%;
        .MuiOutlinedInput-notchedOutline {
          border-color: transparent;
          border-width: 1px;
        }
        .MuiFormControl-root {
          width: 100%;
          margin: 8px 0;
          .MuiInputLabel-outlined.MuiInputLabel-shrink {
            padding: 2px;
            transform: translate(9px, -7px) scale(0.65);
            background-color: white;
          }
          .MuiFormLabel-root {
            font-family: "Raleway", sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 21px;
            color: #667c8a;
          }
          .MuiInputLabel-outlined {
            transform: translate(14px, 12px) scale(1);
          }
          .MuiOutlinedInput-notchedOutline {
            border-color: transparent;
            border-width: 1px;
          }
          .MuiOutlinedInput-input {
            font-family: "Raleway", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
            background-color: white;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #667c8a;
            text-align: start;
            padding: 11px 15px;
            border: 1px solid #667c8a;
            border-radius: 3px;
          }
        }
      }
      .text-input {
        width: 100%;

        input:-webkit-autofill {
          box-shadow: 0 0 0px 1000px #fff inset;
          -webkit-text-fill-color: #667c8a;
          transition: background-color 5000s ease-in-out 0s;
        }
        label {
          font-weight: normal;
          font-size: 18px;
          line-height: 21px;
          color: #667c8a;
        }
        .input {
          border-radius: 3px;
          min-height: 40px;
          height: max-content;
          padding: 11px 15px;
          font-weight: 500;
          font-family: "Raleway", sans-serif;
          font-size: 18px;
          line-height: 21px;
          border: 1px solid #667c8a;
          color: #667c8a !important;

          &:focus ~ label,
          &:not([value=""]) ~ label {
            top: 0px;
            left: 8px;
            font-size: 12px;
            line-height: 14px;
            background-color: white;
            padding-left: 5px;
            padding-right: 5px;
            height: none;
            color: #667c8a;
          }

          // &:focus ~ label {
          //   color: #ba7f45;
          // }

          // &:focus,
          // &:valid ~ label,
          // &:not([value=""]) ~ label {
          //   outline: none;
          //   border-color: #ba7f45;
          //   box-shadow: none;
          // }
        }
      }
      .redBtn {
        margin-top: 17px;
      }
    }
  }
}
